<template>
  <div class="homeCard">
    <div class="row text-center mt-3">
      <div class="col" v-for="product in products" :key="product.id">
        <div
          id="view1"
          class="card col mx-5 m-auto bg-black text-light mt-3"
          v-show="showView1"
        >
          <img
            :src="product.Image"
            class="card-img-top"
            alt="Hoodie"
            loading="lazy"
          />
          <div class="card-body">
            <h3 class="card-title">{{ product.name }}</h3>
            <div class="row">
              <p class="card-text col-6">
                Color:
                <span>{{ product.color }}</span>
              </p>
              <p class="card-text col-6">{{ product.size }}</p>
            </div>
            <p class="card-text">{{ product.price }} .EL</p>
            <p
              class="card-text bg-info text-black d-flex align-items-center justify-content-center"
            >
              <i class="fa-solid fa-barcode me-2"></i>
              {{ product.code }}
            </p>
            <div class="mt-3 text-center">
              <button class="btn btn-success" @click="orderProduct(product)">
                Buy Now |
                <i class="fa-solid fa-cart-shopping fa-xl"></i>
              </button>
            </div>
          </div>
        </div>
      </div>
      <div class="row text-center mt-3">
        <div class="col" v-for="product in products" :key="product.id">
          <div
            id="hidden1"
            class="card col mx-5 m-auto bg-black"
            aria-hidden="true"
            v-show="!showView1"
          >
            <img src="" class="card-img-top" alt="..." />
            <div class="card-body">
              <h4 class="card-title placeholder-glow">
                <span class="placeholder col-6"></span>
              </h4>
              <p class="card-text placeholder-glow">
                <span class="placeholder col-7"></span>
                <span class="placeholder col-4"></span>
                <span class="placeholder col-4"></span>
                <span class="placeholder col-6"></span>
                <span class="placeholder col-8"></span>
              </p>
              <a
                class="btn btn-success disabled placeholder col-6"
                aria-disabled="true"
              ></a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HomeCard",
  data() {
    return {
      showView1: true,
      products: [
        {
          id: 1,
          name: "Hoodie",
          color: "Black",
          size: "Size : XL,2XL",
          price: 250,
          code: "HB100",
          Image: require("../assets/Hoodie/data/j.jpg"),
        },
        {
          id: 2,
          name: "Hoodie",
          color: "Mint Green",
          size: "Size : XL,2XL",
          price: 250,
          code: "HM110",
          Image: require("../assets/Hoodie/data/d.jpg"),
        },
      ],
    };
  },
  methods: {
    onLoadFunction() {
      this.showView1 = true; // إظهار view1
    },
    orderProduct(product) {
      const message = `أنا مهتم بشراء المنتج: ${product.name} . لون: ${product.color}. الكود: ${product.code}`;
      const whatsappLink = `https://wa.me/+201204886348?text=${encodeURIComponent(
        message
      )}`;
      window.open(whatsappLink, "blank");
    },
  },
  mounted() {
    this.onLoadFunction();
  },
};
</script>
<style lang="scss" scoped>
$size: 21px;
$ss: large;
$font: bold;
$rr: 5px;
p {
  font-size: $size;
  font-weight: $font;
  border-radius: $rr;
  span {
    font-size: $ss;
  }
}
</style>
